import styled from 'styled-components'


const PageHeader = styled.div`
  padding: 48px 0;
  overflow: hidden;
  position: relative;

  h1 {
    font-weight: 200;
    font-size: 36px;
    line-height: 40px;
    margin: 0;
  }

  p {
    margin-top: 8px;
  }
`

export default PageHeader



